.cards-info {
  padding: 25px 0 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 50px;
    margin-top: 40px;
  }
  &__item {
    text-align: center;
    &-img {
      img {
        max-width: 100%;
      }
    }
    &-title {
      font-weight: 900;
      font-size: 32px;
      line-height: 120%;
      color: #797979;
      margin-top: 25px;
    }
    &-text {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      color: #373737;
      margin-top: 20px;
    }
  }
  &__desc {
   p {
     font-weight: 300;
     font-size: 20px;
     line-height: 180%;
     color: #373737;
     margin-top: 30px;
     position: relative;
     &:before{
       content: '';
       display: inline-block;
       transform: translate(0, 1px);
       width: 6px;
       height: 17px;
       background: #EBA707;
       margin-right: 10px;
     }
     &:not(:first-child) {
       margin-top: 30px;
     }
   }
  }
}
.cards-info2 {
  background: #373737;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__img {
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}

@media screen and (max-width: 1200px) {
  .cards-info {
    &__items {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width: 992px) {
  .cards-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .cards-info2 {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .cards-info {
    padding: 15px 0 0;
    &__title {
      h2 {
        font-size: 22px;
      }
    }
    &__items {
      margin-top: 30px;
      gap: 30px;
    }
    &__item {
      &-title {
        font-size: 25px;
      }
      &-text {
        font-size: 14px;
        margin-top: 5px;
      }
    }
    &__desc {
      p {
        font-size: 14px;
      }
    }
  }
  .cards-info2 {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__img {
      img {
        &:nth-child(1) {
          display: none;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
  }
}