.print-info3 {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 30px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:not(:first-child) {
        margin-top: 30px;
      }
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
}
.print-desc {
  background: #414141;
  padding: 50px 0;
  &__inner {
    display: flex;
  }
  &__left {
    width: 470px;
    margin-right: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__right {
    width: calc(100% - 550px);
  }
  &__bottom {
    margin-top: 30px;
  }
  &__title {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    i {
      flex-shrink: 0;
      width: 70px;
      img {
        width: 100%;
      }
    }
    h2 {
      font-size: 40px;
      line-height: 130%;
      margin-left: 30px;
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      position: relative;
      &:not(:first-child) {
        margin-top: 30px;
      }
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
  &__list {
    li {
      font-size: 20px;
      line-height: 130%;
      padding-left: 35px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 9px;
        height: 9px;
        background: #EBA707;
      }
      &:not(:first-child) {
        margin-top: 5px;
      }
    }
  }
}
