.print-price {
  background: #414141;
  padding: 50px 0 25px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #EBA707;
      }
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 65px;
    width: calc(100% + 25px);
  }
  &__item {
    max-width: 400px;
    display: flex;
    margin: 0 30px 40px 0;
    &-number {
      font-weight: 900;
      font-size: 96px;
      line-height: 90%;
      color: #EBA707;
    }
    &-desc {
      margin-left: 25px;
    }
    &-title {
      font-weight: 900;
      font-size: 34px;
      line-height: 120%;
    }
    &-text {
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      margin-top: 10px;
    }
  }
}

@media screen and (max-width: 1350px) {
  .print-price {
    padding: 50px 0;
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px;
      width: 100%;
    }
    &__item {
      margin: 0;
      max-width: 100%;
    }
  }
}

