.information {
  padding: 169px 0 0px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 490px);
    padding: 100px 0 50px;
    position: relative;
    &-wrapp {
      position: relative;
      z-index: 2;
    }
  }
  &__right {
    width: 370px;
    margin-left: 120px;
    background: #575757;
    position: relative;
    padding: 40px 50px;
    &-wrapp {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      position: fixed;
      width: 270px;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 100%;
      width: 5000px;
      height: 100%;
      background: #575757;
    }
  }
  &__img {
    position: absolute;
    top: 25px;
    right: -50px;
    pointer-events: none;
    width: 371px;
    height: auto;
    img {
      width: 100%;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    color: #EBA707;
  }
  &__desc {
    max-width: 570px;
    margin-top: 50px;
    p {
      font-size: 18px;
      line-height: 160%;
      color: #565656;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__nav {
    margin-top: 50px;
  }
  &__subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    color: #797979;
    margin-bottom: 30px;
  }
  &__list {
    li {
      font-size: 18px;
      line-height: 130%;
      color: #797979;
      position: relative;
      padding-left: 25px;
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        width: 9px;
        height: 9px;
        background: #EBA707;
      }
      span {
        text-transform: uppercase;
        display: block;
      }
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__right {
    &-link {
      font-weight: 500;
      font-size: 14px;
      line-height: 120%;
      display: block;
      &:hover {
        color: #EBA707;
      }
      &--active {
        background: #EBA707;
        border: 1px solid #EBA707;
        padding: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover {
          &:hover {
            background: #000;
          }
        }
      }
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    &-nav {
      width: 100%;
      position: relative;
      &:not(:first-child) {
        margin-top: 80px;
        &:after {
          content: '';
          position: absolute;
          top: -40px;
          left: 50%;
          transform: translate(-50%, 0);
          width: calc(100% + 30px);
          height: 4px;
          background: #797979;
        }
      }
    }
    &-subtitle {
      font-weight: 900;
      font-size: 20px;
      line-height: 120%;
      margin-bottom: 30px;
    }
  }
}

@media screen  and (max-width: 1360px) {
  .information {
    &__left {
      padding-top: 525px;
      width: calc(100% - 410px);
    }
    &__right {
      width: 370px;
      margin-left: 40px;
    }
    &__img {
      top: 15px;
      left: 50%;
      transform: translate(-50%, 0);
    }
    &__desc {
      max-width: 100%;
    }
  }
}

@media screen  and (max-width: 992px) {
  .information {
    padding: 154px 0 0 0;
    &__left {
      padding-top: 400px;
      width: calc(100% - 350px);
    }
    &__right {
      width: 320px;
      margin-left: 30px;
      padding: 20px 0 20px 25px;
    }
    &__img {
      top: 0;
      width: 275px;
    }
    &__title {
      font-size: 32px;
    }
  }
}

@media screen  and (max-width: 768px) {
  .information {
    padding: 15px 0 30px;
    &__left {
      padding-bottom: 0;
      width: 100%;
    }
    &__right {
      display: none;
      &-nav {
        &:not(:first-child) {
          margin-top: 35px;
          &:after {
            display: none;
          }
        }
      }
      &-subtitle {
        background: #444444;
        padding: 20px 15px;
        width: calc(100% + 30px);
        margin-left: -15px;
      }
      &-list {
        display: flex;
        flex-direction: column;
      }
    }
    &__title {
      font-size: 24px;
      br {
        display: none;
      }
    }
    &__nav {
      margin-top: 30px;
    }
    &__subtitle {
      font-size: 20px;
      text-align: center;
    }
    &__list {
      li {
        font-size: 14px;
        padding-left: 17px;
        &:after {
          top: 5px;
          width: 6px;
          height: 6px;
        }
        span {
          font-weight: 600;
          margin-bottom: 7px;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
  }
}