.print-advantages {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 40px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      margin-top: 30px;
      position: relative;
      span {
        font-weight: 700;
      }
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #7A7A7A;
    margin-left: 30px;
  }
}

@media screen and (max-width: 992px) {
  .print-advantages {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .print-advantages {
    padding: 15px 0 0;
    &__title {
      h2 {
        font-size: 22px;
      }
    }
    &__desc {
      p {
        font-size: 14px;
      }
    }
    &__bottom {
      background: #606060;
      padding: 30px 0;
      flex-direction: column;
    }
    &__btn {
      background: transparent;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
      color: #fff;
    }
  }
}