.ads-advantages {
  padding: 50px 0;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 25px;
  }
  &__item {
    text-align: center;
   &-img {
     width: 50px;
     height: 50px;
     margin: 0 auto;
     background: #EBA707;
     border-radius: 50%;
     display: flex;justify-content: center;
     align-items: center;
     img {
       max-width: 100%;
       max-height: 100%;
     }
   }
    &-title {
      font-weight: 900;
      font-size: 32px;
      line-height: 130%;
      color: #797979;
      margin-top: 5px;
    }
    &-desc {
      font-size: 16px;
      line-height: 130%;
      color: #373737;
      margin-top: 5px;
    }
  }
}
.ads-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 40px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
}
.ads-stages {
  background: #414141;
  padding: 50px 0 25px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 65px;
    width: calc(100% + 25px);
  }
  &__item {
    max-width: 400px;
    display: flex;
    margin: 0 25px 40px 0;
    &-number {
      font-weight: 900;
      font-size: 96px;
      line-height: 90%;
      color: #EBA707;
    }
    &-desc {
      margin-left: 25px;
    }
    &-title {
      font-weight: 900;
      font-size: 34px;
      line-height: 120%;
    }
    &-text {
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      margin-top: 10px;
    }
    &--big {
      max-width: calc(50% - 25px);
    }
  }
}


@media screen and (max-width: 1350px) {
  .ads-stages {
    padding: 50px 0;
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px;
      width: 100%;
    }
    &__item {
      margin: 0;
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .ads-advantages {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
  .ads-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .ads-stages {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      &-number {
        font-size: 80px;
      }
      &-title {
        font-size: 25px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .ads-advantages {
    padding: 30px 0;
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      &-title {
        font-size: 25px;
      }
      &-desc {
        font-size: 14px;
      }
    }
  }
  .ads-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
      }
    }
  }
  .ads-stages {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 22px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__item {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -20px;
        left: 0;
        width: 100%;
        height: 1px;
        background: #C7C7C7;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
}