.sings-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 40px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
}
.sings-info2 {
  background: #414141;
  padding: 50px 0;
  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }
  &__left {
    max-width: 460px;
    margin-right: 80px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__title {
    display: flex;
    i {
      width: 48px;
      margin-top: 5px;
      img {
        width: 100%;
      }
    }
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      margin-left: 35px;
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    margin-top: 25px;
  }
}
.sings-kinds {
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      text-transform: uppercase;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__subtitle {
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    color: #373737;
    text-align: center;
    max-width: 850px;
    margin: 30px auto 0;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px 60px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    &-desc {
      margin-left: 15px;
    }
    &-title {
      font-weight: 900;
      font-size: 30px;
      line-height: 120%;
      color: #797979;
      padding-left: 15px;
    }
    &-text {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      color: #373737;
      margin-top: 10px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .sings-info2 {
    &__left {
      max-width: 400px;
      width: 100%;
      margin-right: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .sings-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .sings-info2 {
    &__inner {
      display: block;
      padding-top: 70px;
    }
    &__left {
      max-width: 600px;
      margin: 0 auto;
    }
    &__right {
      margin-top: 30px;
    }
    &__title {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100%;
      justify-content: center;
      text-align: center;
      i {
        display: none;
      }
      h2 {
        font-size: 32px;
        margin-left: 0;
      }
    }
  }
  .sings-kinds {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 40px;
    }
    &__item {
      &-title {
        font-size: 25px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .sings-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
      }
    }
  }
  .sings-info2 {
    padding: 30px 0;
    &__inner {
      padding-top: 55px;
    }
    &__left {
      max-width: 300px;
    }
    &__title {
      h2 {
        font-size: 20px;
      }
    }
    &__desc {
      font-size: 14px;
    }
  }
  .sings-kinds {
    padding: 15px 0 0 0;
    &__title {
      h2 {
        font-size: 22px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__subtitle {
      font-size: 14px;
    }
    &__item {
      position: relative;
      &:not(:first-child) {
        &:after {
          content: '';
          position: absolute;
          top: -20px;
          left: 0;
          width: 100%;
          height: 1px;
          background: #C7C7C7;
        }
      }
      &-img {
        width: 60px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      &-text {
        font-size: 14px;
      }
    }
  }
}