.print-advantages2 {
  padding: 50px 0 25px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 30px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:not(:first-child) {
        margin-top: 30px;
      }
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
}
.print-cards {
  padding: 25px 0 50px;
}

@media screen and (max-width: 992px) {
  .print-advantages2 {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .print-flex {
    display: flex;
    flex-direction: column;
  }
  .print-advantages2 {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
  }
  .print-cards {
    padding: 15px 0;
    order: -1;
  }
}