.engraving {
  padding: 50px 0;
  &__info {
    font-weight: 300;
    font-size: 32px;
    line-height: 160%;
    text-align: center;
    color: #565656;
    margin-top: 30px;
  }
}

@media screen and (max-width: 768px) {
  .engraving {
    padding: 30px 0;
    &__info {
      font-size: 14px;
      margin-top: 20px;
    }
  }
}