.panels-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 40px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
}
.panels-advantages {
  background: #414141;
  padding: 50px 0;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__left {
    width: calc(100% - 460px);
  }
  &__right {
    width: 400px;
    margin-left: 60px;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
    i {
      width: 65px;
      img {
        width: 100%;
      }
    }
    h2 {
      font-size: 40px;
      line-height: 120%;
      text-transform: uppercase;
      margin-left: 15px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  &__item {
    position: relative;
    padding-left: 25px;
    &:after {
      content: '';
      position: absolute;
      top: 10px;
      left: 0;
      width: 9px;
      height: 9px;
      background: #EBA707;
    }
    &-title {
      font-weight: 700;
      font-size: 28px;
      line-height: 130%;
    }
    &-desc {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      margin-top: 10px;
    }

    &--100 {
      grid-column: 1/3;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
  &__item2 {
    display: flex;
    align-items: center;
    &-img {
      width: 26px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-weight: 300;
      font-size: 20px;
      line-height: 130%;
      margin-left: 30px;
    }
  }
  &__info {
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    margin-top: 30px;
  }
}
.panels-advantages2 {
  background: #fff;
  &__title {
    h2 {
      color: #797979;
    }
  }
  &__item {
    &-title {
      color: #797979;
    }
    &-desc {
      color: #373737;
    }
  }
  &__item2 {
    &-desc {
      color: #373737;
    }
  }
  &__info {
    color: #797979;
  }
}

@media screen and (max-width: 1300px) {
  .panels-advantages {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .panels-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .panels-advantages {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      &--100 {
        grid-column: 1/2;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .panels-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
      }
    }
  }
  .panels-advantages {
    padding: 30px 0;
    &__title {
      i {
        width: 35px;
      }
      h2 {
        font-size: 24px;
      }
    }
    &__items {
      gap: 25px;
    }
    &__item {
      &:after {
        top: 7px;
        width: 6px;
        height: 6px;
      }
      &-title {
        font-size: 18px;
      }
      &-desc {
        font-size: 14px;
        margin-top: 5px;
      }
    }
    &__items2 {
      gap: 25px;
    }
    &__item2 {
      &-img {
        width: 22px;
      }
      &-desc {
        font-size: 14px;
        margin-left: 20px;
      }
    }
    &__info {
      text-align: center;
      font-size: 14px;
    }
  }
}