.stickers-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    &-desc {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #151515;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
    &-list {
      li {
        font-weight: 300;
        font-size: 20px;
        line-height: 150%;
        color: #151515;
        padding-left: 18px;
        position: relative;
        &:not(:first-child) {
          margin-top: 7px;
        }
        &:after {
          content: '';
          position: absolute;
          top: 12px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #EBA707;
        }
      }
    }
  }
  &__desc {
    max-width: 920px;
    margin: 30px auto 0;
    font-size: 20px;
    line-height: 150%;
    font-weight: 300;
    color: #151515;
    text-align: center;
  }
}
.stickers-production {
  background: #434343;
  padding: 50px 0;
  position: relative;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px 70px;
    margin-top: 40px;
    max-width: 840px;
    position: relative;
    z-index: 2;
  }
  &__item {
    position: relative;
    background: #797979;
    padding: 30px 20px;
    &--100 {
      grid-column: 1/3;
    }
    &-title {
      font-weight: 700;
      font-size: 36px;
      line-height: 120%;
      &2 {
        padding-left: 150px;
      }
    }
    &-info {
      padding-left: 150px;
      &2 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
      }
    }
    &-img {
      position: absolute;
      left: -35px;
      bottom: 30px;
      width: 170px;
      img {
        width: 100%;
      }
      &2 {
        position: absolute;
        right: -35px;
        bottom: 40px;
        width: 170px;
        img {
          width: 100%;
        }
      }
    }
    &-subtitle {
      font-weight: 800;
      font-size: 18px;
      line-height: 120%;
      padding-left: 10px;
    }
    &-nav {
      margin-top: 20px;
    }
    &-list {
      margin-top: 5px;
      li {
        font-size: 18px;
        line-height: 120%;
        padding-left: 10px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 10px;
          left: 0;
          width: 3px;
          height: 3px;
          background: #FFFFFF;
          border-radius: 50%;
        }
      }
    }

  }
  &__img {
    position: absolute;
    z-index: 0;
    pointer-events: none;
    right: 0;
    bottom: 100px;
    width: 540px;
    img {
      width: 100%;
    }
  }
}
.stickers-price {
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #373737;
      text-transform: uppercase;
      position: relative;
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #373737;
    max-width: 920px;
    margin: 30px auto 0;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-number {
      font-weight: 900;
      font-size: 96px;
      line-height: 90%;
      color: #EBA707;
    }
    &-desc {
      font-weight: 900;
      font-size: 30px;
      line-height: 130%;
      color: #565656;
      margin-left: 20px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: #7A7A7A;
    margin-left: 30px;
  }
}

@media screen and (max-width: 1250px) {
  .stickers-price {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media screen and (max-width: 992px) {
  .stickers-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }
  .stickers-production {
    &__items {
      grid-template-columns: 1fr;
      margin-top: 300px;
      gap: 40px;
    }
    &__item {
      background: transparent;
      padding: 0;
      &--100 {
        grid-column: 1/1;
      }
      &-title2 {
        padding-left: 0;
      }
      &-info {
        display: flex;
        align-items: center;
        padding: 0;
        margin-top: 25px;
        &2 {
          display: block;
          .stickers-production__info-left {
            display: flex;
            align-items: center;
          }
          .stickers-production__info-right {
            display: flex;
            align-items: center;
            margin-top: 15px;
          }
        }
      }
      &-img {
        position: static;
        width: 116px;
        flex-shrink: 0;
        &2 {
          position: static;
          width: 116px;
        }
      }
      &-nav {
        margin: 0 0 0 15px;
      }
    }
    &__img {
      z-index: 2;
      top: 85px;
      bottom: unset;
      width: 310px;
    }
  }
  .stickers-price {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .stickers-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-size: 28px;
      }
    }
    &__items {
      margin-top: 20px;
    }
    &__item {
      &-desc {
        font-size: 14px;
      }
      &-list {
       li {
         font-size: 14px;
         &:after {
           top: 7px;
         }
       }
      }
    }
    &__desc {
      font-size: 14px;
      margin-top: 20px;
    }
  }
  .stickers-production {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 28px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__img {
      top: 50px;
    }
    &__item {
      &:after {
        content: '';
        position: absolute;
        top: -20px;
        left: 0;
        width: 100%;
        height: 1px;
        background: #EBA707;
      }
      &-title {
        font-size: 25px;
      }
      &-list {
        li {
          font-size: 14px;
        }
      }
    }
  }
  .stickers-price {
    padding: 30px 0 0 0 ;
    &__title {
     h2 {
       font-size: 22px;
     }
    }
    &__desc {
      font-size: 14px;
      margin: 20px auto 0;
    }
    &__items {
      gap: 15px;
      margin-top: 20px;
    }
    &__item {
      display: block;
      text-align: center;
      &-number {
        font-size: 80px;
      }
      &-desc {
        font-size: 20px;
        font-weight: 400;
        color: #797979;
      }
    }
    &__bottom {
      margin-top: 20px;
      padding: 30px 0;
      background: #606060;
      flex-direction: column;
    }
    &__btn {
      background: transparent;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
    }
  }
}

@media screen and (max-width: 375px) {
  .stickers-production {
    &__item {
      &-img {
        width: 60px;
        &2 {
          width: 60px;
        }
      }
    }
  }
  .stickers-price {
    &__items {
      grid-template-columns: 1fr;
    }
  }
}