.menu-cards {
  padding: 50px 0 25px;
  &__card-list {
    margin-top: 15px;
    li {
      font-size: 18px;
      line-height: 120%;
      font-weight: 300;
    }
  }
}
.menu-desc {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #151515;
    margin-top: 30px;
    position: relative;
    &:before{
      content: '';
      display: inline-block;
      transform: translate(0, 1px);
      width: 6px;
      height: 17px;
      background: #EBA707;
      margin-right: 10px;
    }
  }
}
.menu-info {
  background: #414141;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 60px;
  }
  &__item {
    display: flex;
    &-number {
      font-weight: 900;
      font-size: 96px;
      line-height: 90%;
      color: #EBA707;
    }
    &-desc {
      margin-left: 20px;
    }
    &-title {
      font-weight: 900;
      font-size: 30px;
      line-height: 130%;
    }
    &-text {
      font-weight: 300;
      font-size: 20px;
      line-height: 120%;
      margin-top: 10px;
    }
  }
}
.souvenirs-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    max-width: 920px;
    margin: 30px auto 0;
    font-weight: 300;
    font-size: 20px;
    line-height: 160%;
    color: #151515;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  &__item {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #373737;
    margin-top: 30px;
    position: relative;
    &:before{
      content: '';
      display: inline-block;
      transform: translate(0, 1px);
      width: 6px;
      height: 17px;
      background: #EBA707;
      margin-right: 10px;
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 160%;
    color: #373737;
    margin: 30px auto 0;
    max-width: 920px;
    text-align: center;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: #7A7A7A;
    margin-left: 30px;
  }
}

@media screen and (max-width: 1350px) {
  .menu-info {
    &__items {
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .menu-cards {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width: 992px) {
  .menu-desc {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .menu-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__item {
      &-number {
        font-size: 80px;
      }
      &-title {
        font-size: 25px;
      }
    }
  }
  .souvenirs-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .menu-cards {
    padding: 30px 0 15px;
  }
  .menu-desc {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      font-size: 14px;
      margin-top: 20px;
    }
  }
  .menu-info {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 22px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -20px;
        left: 0;
        width: 100%;
        height: 1px;
        background: #EBA707;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
  .souvenirs-info {
    padding: 30px 0 0 0;
    &__title {
      h2 {
        font-size: 22px;
      }
    }
    &__desc {
      font-size: 14px;
      font-weight: 600;
      margin-top: 20px;
    }
    &__items {
      gap: 20px;
    }
    &__item {
      font-size: 14px;
    }
    &__subtitle {
      font-size: 14px;
      margin-top: 20px;
    }
    &__bottom {
      background: #606060;
      padding: 30px 0;
      flex-direction: column;
    }
    &__btn {
      background: transparent;
    }
    &__info {
      margin: 10px 0 0 0;
      font-size: 14px;
      color: #fff;
    }
  }
}

@media screen and (max-width: 576px) {
  .menu-cards {
    &__card-list {
      margin-top: 10px;
      li {
        font-size: 10px;
      }
    }
  }
}