.stands-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      text-transform: uppercase;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px 60px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    &-desc {
      margin-left: 15px;
    }
    &-title {
      font-weight: 900;
      font-size: 30px;
      line-height: 120%;
      color: #797979;
      padding-left: 15px;
    }
    &-text {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      color: #373737;
      margin-top: 10px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: #7A7A7A;
    margin-left: 30px;
  }
}

@media screen and (max-width: 992px) {
  .stands-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 40px;
    }
    &__item {
      &-title {
        font-size: 25px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .stands-info {
    padding: 15px 0 0 0;
    &__title {
      h2 {
        font-size: 22px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__item {
      position: relative;
      &:not(:first-child) {
        &:after {
          content: '';
          position: absolute;
          top: -20px;
          left: 0;
          width: 100%;
          height: 1px;
          background: #C7C7C7;
        }
      }
      &-text {
        font-size: 14px;
      }
    }
    &__bottom {
      background: #606060;
      padding: 30px 0;
      flex-direction: column;
    }
    &__btn {
      background: transparent;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
      color: #FFFFFF;
    }
  }
}