.cutting-advantages2 {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #565656;
      }
    }
  }
  &__desc {
    margin-top: 30px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:not(:first-child) {
        margin-top: 30px;
      }
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
}
.cutting-table {
  background: #434343;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #EBA707;
      }
    }
  }
  &__text {
    font-weight: 300;
    font-size: 20px;
    line-height: 160%;
    max-width: 1180px;
    margin: 30px auto 0;
    text-align: center;
  }
  &__content {
    margin-top: 60px;
  }
  &__heading {
    display: flex;
    &-left {
      background: #565656;
      padding: 0 30px;
      font-size: 18px;
      line-height: 120%;
      width: 33%;
      margin-right: 20px;
      display: flex;
      align-items: center;
    }
    &-right {
      background: #565656;
      padding: 30px 30px 10px;
      width: calc(100% - 33% - 20px);
      text-align: center;
      align-items: center;
    }
    &-text {
      font-size: 18px;
      line-height: 120%;
      text-align: center;
    }
    &-numbers {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      gap: 10px;
      margin-top: 20px;
      span {
        font-size: 16px;
        line-height: 120%;
      }
    }
  }
  &__desc {
    height: 50px;
    display: flex;
    margin-top: 10px;
    &-left {
      width: 33%;
      margin-right: 20px;
      background: #373737;
      padding: 0 30px;
      font-size: 16px;
      line-height: 120%;
      display: flex;
      align-items: center;
    }
    &-right {
      width: calc(100% - 33% - 20px);
      background: #373737;
      padding: 15px 30px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
      gap: 10px;
      text-align: center;
      align-items: center;
      span {
        font-size: 16px;
        line-height: 120%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .cutting-advantages2 {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .cutting-table {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__heading {
      height: 106px;
      &-left {
        font-size: 12px;
        margin-right: 5px;
        padding: 0 5px;
        justify-content: center;
        text-align: center;
      }
      &-right {
        width: calc(100% - 33% - 5px);
        padding: 0 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      &-text {
        font-size: 12px;
      }
      &-numbers {
        width: 100%;
        span {
          font-size: 12px;
        }
      }
    }
    &__desc {
      margin-top: 5px;
      &-left {
        font-size: 12px;
        margin-right: 5px;
        padding: 0 5px;
        justify-content: center;
        text-align: center;
      }
      &-right {
        width: calc(100% - 33% - 5px);
        padding: 0 5px;
        span {
          font-size: 12px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .cutting-advantages2 {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
  }
  .cutting-table {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 26px;
      }
    }
    &__text {
      font-size: 14px;
      margin-top: 20px;
    }
    &__content {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 375px) {
  .cutting-table {
    &__heading {
      height: 70px;
      &-left {
        font-size: 8px;
      }
      &-text {
        font-size: 8px;
      }
      &-numbers {
        span {
          font-size: 8px;
        }
      }
    }
    &__desc {
      &-left {
        font-size: 8px;
      }
      &-right {
        span {
          font-size: 8px;
        }
      }
    }
  }
}