.badges-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      text-transform: uppercase;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 40px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
}
.our-badges {
  background: #414141;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    h2 {
      font-weight: 500;
      font-size: 48px;
      line-height: 120%;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 40px;
    width: calc(100% + 20px);
  }
  &__item {
    max-width: 207px;
    text-align: center;
    margin: 0 20px 20px 0;
    &-img {
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      margin: 0 auto;
      img {
        max-width: 100%;
      }
    }
    &-desc {
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      margin-top: 22px;
    }
  }
}
.badges-info2 {
  padding: 50px 0;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__left {
    width: calc(100% - 730px);
    img {
      width: 100%;
    }
  }
  &__right {
    width: 630px;
    margin-left: 100px;
  }
  &__title {
    display: flex;
    position: relative;
    i {
      position: absolute;
      top: 0;
      left: -107px;
      width: 77px;
      img {
        width: 100%;
      }
    }
    h2 {
      font-weight: 700;
      font-size: 44px;
      line-height: 120%;
      color: #797979;
      display: inline-block;
      position: relative;
      padding-bottom: 12px;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: #C7C7C7;
      }
    }
    &-mob {
      display: none;
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    color: #373737;
    margin-top: 30px;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: #7A7A7A;
    margin-left: 30px;
  }
}

@media screen and (max-width: 1250px) {
  .badges-info2 {
    &__left {
      width: 480px;
      margin-right: 40px;
    }
    &__right {
      width: calc(100% - 520px);
    }
  }
}

@media screen and (max-width: 1200px) {
  .our-badges {
    &__items {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 30px;
    }
    &__item {
      margin: 0 auto;
    }
  }
}

@media screen and (max-width: 992px) {
  .badges-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .our-badges {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .badges-info2 {
    &__inner {
      display: block;
      position: relative;
      margin-bottom: 30px;
    }
    &__left {
      width: 100%;
      margin: 0 auto;
      max-width: 600px;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0 0;
    }
    &__title {
     display: none;
     &-mob {
       display: block;
       font-weight: 700;
       font-size: 32px;
       line-height: 120%;
       text-transform: uppercase;
       color: #565656;
       text-align: center;
       margin-bottom: 30px;
     }
    }
  }
}

@media screen and (max-width: 768px) {
  .badges-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
      }
    }
  }
  .our-badges {
    padding: 30px 0;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 24px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
    }
    &__item {
      max-width: 100%;
      margin: 0;
      display: flex;
      align-items: center;
      text-align: start;
      &-img {
        margin: 0;
        flex-shrink: 0;
      }
      &-desc {
        margin:0 0 0 30px;
        font-size: 16px;
      }
    }
  }
  .badges-info2 {
    padding: 30px 0 0 0;
    &__title-mob {
      font-size: 18px;
    }
    &__left {
      max-width: 300px;
    }
    &__desc {
      font-size: 14px;
    }
    &__bottom {
      background: #606060;
      flex-direction: column;
      padding: 30px 0;
    }
    &__info {
      margin: 10px 0 0 0;
    }
  }
}