.print-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__subtitle {
    font-weight: 300;
    font-size: 20px;
    line-height: 32px;
    text-align: center;
    max-width: 920px;
    margin: 30px auto 0;
    color: #151515;
  }
  &__desc {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin-top: 30px;
    &-title {
      font-weight: 500;
      font-size: 30px;
      line-height: 120%;
      color: #565656;
    }
    &-text {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #151515;
      margin-top: 10px;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
}
.print-info2 {
  background: #414141;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #fff;
      padding-bottom: 12px;
      text-transform: uppercase;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
    margin-top: 30px;
  }
  &__item {
    &-title {
      font-weight: 700;
      font-size: 28px;
      line-height: 120%;
    }
    &-list {
      margin-top: 15px;
      li {
        font-weight: 300;
        font-size: 20px;
        line-height: 120%;
        padding-left: 21px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 10px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #EBA707;
        }
        &:not(:first-child) {
          margin-top: 7px;
        }
      }
    }
    &-text {
      font-weight: 300;
      font-size: 20px;
      line-height: 120%;
      margin-top: 30px;
    }
  }
  &__desc {
    max-width: 920px;
    margin: 30px auto 0;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    line-height: 160%;
  }
}

@media screen and (max-width: 992px) {
  .print-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__desc {
      grid-template-columns: 1fr;
    }
  }
  .print-info2 {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 30px;
    }
    &__item {
      &-title {
        font-size: 24px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .print-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-size: 22px;
      }
    }
    &__subtitle {
      margin-top: 20px;
      font-size: 14px;
    }
    &__desc {
      margin-top: 20px;
      &-title {
        font-size: 18px;
        text-align: center;
        font-weight: 700;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
  .print-info2 {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 22px;
      }
    }
    &__item {
      &-title {
        font-size: 18px;
        font-weight: 900;
        text-align: center;
      }
      &-list {
        li {
          font-size: 14px;
        }
      }
    }
    &__desc {
      font-size: 14px;
    }
  }
}